<template>
  <div>
    <Header></Header>
    <div class="main">
      <div class="user_info">
        <el-row>
          <el-col :span="4">
            <div class="user_img">
              <img :src="userInfo.avatar == null ? userImg : userInfo.avatar" />
            </div>
            <p
              v-if="loginId != $route.params.id"
              style="margin-top: 5px"
              class="author_follow"
            >
              <span @click="authorFollow" v-if="!isFollow"
                ><i class="el-icon-plus"></i></span
              >
              <span @click="undoAuthorFollow" v-else></span>
            </p>
          </el-col>
          <el-col :span="15">
            <div class="user_title">
              <span>{{ userInfo.username }}</span>
            </div>
            <div class="user_brief">
              <span><b></b>{{ userInfo.followedCount }}</span
              ><span><b></b>{{ userInfo.followerCount }}</span
              ><span
                ><b></b>{{ userInfo.createTime.split("T")[0] }}</span
              ><br />
              <div class="user_brief_content">
                <span><b></b></span>
                <span>
                  {{ userInfo.summary }}
                </span>
              </div>
            </div>
          </el-col>
          <el-col :span="5">
            <div class="user_achievement">
              <div class="user_achievement_title">
                <span style="display: inline-block; line-height: 50px"
                  ></span
                >
              </div>
              <div class="user_achievement_info">
                <div class="user_achievement_info_count">
                  <div class="user_achievement_info_item">
                    <div class="user_achievement_info_item_label"></div>
                    <div class="user_achievement_info_item_number">
                      <span class="number_style">{{
                        userOther.thumbsUpCount
                      }}</span>
                    </div>
                  </div>
                  <div class="user_achievement_info_item">
                    <div class="user_achievement_info_item_label"></div>
                    <div class="user_achievement_info_item_number">
                      <span class="number_style">{{
                        userOther.viewsCount
                      }}</span>
                    </div>
                  </div>
                  <div class="user_achievement_info_item">
                    <div class="user_achievement_info_item_label"></div>
                    <div class="user_achievement_info_item_number">
                      <span class="number_style">{{
                        userOther.originalCount
                      }}</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </el-col>
        </el-row>
      </div>
      <div class="original_body">
        <el-col :span="18">
          <div class="article_condition">
            <el-menu
              v-if="this.$route.params.id == loginId"
              :default-active="conditionIndex"
              class="el-menu-demo"
              mode="horizontal"
              active-text-color="#3EAF7C"
              @select="handleSelect2"
            >
              <el-menu-item
                v-for="data in conditionList"
                :key="data.index"
                :index="data.index"
                >{{ data.category }}</el-menu-item
              >
            </el-menu>
            <el-menu
              v-else
              :default-active="conditionIndex"
              class="el-menu-demo"
              mode="horizontal"
              active-text-color="#3EAF7C"
              @select="handleSelect2"
            >
              <el-menu-item
                v-for="data in otherConditionList"
                :key="data.index"
                :index="data.index"
                >{{ data.category }}</el-menu-item
              >
            </el-menu>
          </div>
          <div
            class="article_main"
            v-show="conditionIndex === '10000'"
            v-for="data in articleList"
            :key="data.id"
          >
            <div v-if="data.firstImg != null" class="article_main_body">
              <p class="article-title">
                <router-link target="_blank" :to="'/article/' + data.id">{{
                  data.title
                }}</router-link>
              </p>
              <p class="article-tag">
                <el-tag
                  size="small"
                  style="color: rgb(62, 175, 124)"
                  color="white"
                  v-if="data.official == 1"
                  ></el-tag
                >
                <el-tag
                  size="small"
                  style="color: rgb(62, 175, 124)"
                  color="white"
                  v-if="data.top == 1"
                  ></el-tag
                >
                <el-tag
                  size="small"
                  style="color: rgb(62, 175, 124)"
                  color="white"
                  v-if="data.essence == 1"
                  ></el-tag
                >
                <el-tag
                  v-for="tag in data.tagList"
                  :key="tag.id"
                  type="info"
                  size="mini"
                  ><router-link target="_blank" :to="'/tag/' + tag.id">{{
                    tag.label
                  }}</router-link></el-tag
                >
              </p>
              <p class="article-describe">
                <router-link target="_blank" :to="'/article/' + data.id">
                  {{ data.content }}</router-link
                >
              </p>
              <p class="article-other">
                <span
                  ><router-link target="_blank" :to="'/user/' + data.authorId"
                    >{{ data.authorUsername }}&nbsp;&nbsp;</router-link
                  ></span
                >
                <span>{{ data.publishTime.split("T")[0] }}</span>
                <span
                  ><i class="el-icon-view"></i>&nbsp;&nbsp;{{
                    data.views
                  }}</span
                >
              </p>
            </div>
            <div v-else class="article_main_body_noImg">
              <p class="article-title">
                <router-link target="_blank" :to="'/article/' + data.id">{{
                  data.title
                }}</router-link>
              </p>
              <p class="article-tag">
                <el-tag
                  size="small"
                  style="color: rgb(62, 175, 124)"
                  color="white"
                  v-if="data.official == 1"
                  ></el-tag
                >
                <el-tag
                  size="small"
                  style="color: rgb(62, 175, 124)"
                  color="white"
                  v-if="data.top == 1"
                  ></el-tag
                >
                <el-tag
                  size="small"
                  style="color: rgb(62, 175, 124)"
                  color="white"
                  v-if="data.essence == 1"
                  ></el-tag
                >
                <el-tag
                  v-for="tag in data.tagList"
                  :key="tag.id"
                  type="info"
                  size="mini"
                  ><router-link target="_blank" :to="'/tag/' + tag.id">{{
                    tag.label
                  }}</router-link></el-tag
                >
              </p>
              <p class="article-describe">
                <router-link target="_blank" :to="'/article/' + data.id">
                  {{ data.content }}</router-link
                >
              </p>
              <p class="article-other">
                <span
                  ><router-link
                    target="_blank"
                    :to="'/user/' + data.authorId"
                    >{{ data.authorUsername }}</router-link
                  >&nbsp;&nbsp;</span
                >
                <span>{{ data.publishTime.split("T")[0] }}</span>
                <span
                  ><i class="el-icon-view"></i>&nbsp;&nbsp;{{
                    data.views
                  }}</span
                >
              </p>
            </div>
            <div v-if="data.firstImg != null" class="article_main_img">
              <router-link target="_blank" :to="'/article/' + data.id"
                ><img :src="data.firstImg"
              /></router-link>
            </div>
          </div>
          <div
            class="question_main"
            v-show="conditionIndex === '10001'"
            v-for="data in questionList"
            :key="data.id"
          >
            <div class="question_main_body">
              <p class="question_main_title">
                <router-link target="_blank" :to="'/question/' + data.id">{{
                  data.title
                }}</router-link>
              </p>
              <p class="question_main_tags">
                <el-tag
                  v-for="tag in data.tagList"
                  :key="tag.id"
                  type="info"
                  size="mini"
                  ><router-link target="_blank" :to="'/tag/' + tag.id">{{
                    tag.label
                  }}</router-link></el-tag
                >
              </p>
              <div class="question_main_other">
                <span>{{ data.commentCount }}&nbsp;</span>
                <span>&nbsp;</span>
                <span>{{ data.views }}</span>
                <div class="other_right">
                  <span
                    ><router-link target="_blank" :to="'/user/' + data.id">{{
                      data.authorUsername
                    }}</router-link></span
                  ><span>&nbsp;</span>
                  <span>{{ data.publishTime.split("T")[0] }}</span>
                </div>
              </div>
            </div>
          </div>
          <div v-show="conditionIndex === '10002'" class="message_main">
            <div class="message_main_thumbsUp">
              <div class="thumbsUp_avatar">
                <img src="@/assets/images/nologin.png" />
              </div>
              <div class="thumbsUp_show">
                <span
                  ><router-link to="/user/1d3a"><b></b></router-link> 
                  </span
                >
              </div>
              <div class="thumbsUp_link">
                <span
                  ></span
                >
              </div>
            </div>
            <div class="message_main_comment">
              <div class="thumbsUp_avatar">
                <img src="@/assets/images/nologin.png" />
              </div>
              <div class="thumbsUp_show">
                <span
                  ><router-link to="/user/1d3a"><b></b></router-link> 
                  </span
                >
              </div>
              <div class="thumbsUp_link">
                <span
                  ></span
                >
              </div>
            </div>
          </div>
          <div class="loading">{{ noMore ? "" : "..." }}</div>
        </el-col>
        <el-col :span="6">
          <div class="article_aside">
            <div
              v-if="this.$route.params.id == loginId"
              class="user_aside_area"
            >
              <div class="article_random_tip">
                <span></span>
                <span
                  @click="sendUserVisible()"
                  style="float: right; margin-right: 15px; font-size: 14px"
                  ></span
                >
              </div>
            </div>
            <div class="article_bulletin_area">
              <div class="article_bulletin_tip">
                <span></span>
                <span
                  @click="followClick"
                  style="float: right; margin-right: 15px; font-size: 14px"
                  ></span
                >
              </div>
            </div>
            <div class="article_random_area">
              <div class="article_random_tip">
                <span></span>
                <span
                  @click="followedClick"
                  style="float: right; margin-right: 15px; font-size: 14px"
                  ></span
                >
              </div>
            </div>
          </div>
        </el-col>
      </div>
    </div>
    <el-drawer title="" :visible.sync="followDrawer" direction="rtl">
      <div class="user_follow">
        <div class="user_follow_item" v-for="data in followData" :key="data.id">
          <img :src="data.avatar == null ? userImg : data.avatar" />
          <span
            ><router-link target="_blank" :to="'/user/' + data.id">{{
              data.username
            }}</router-link></span
          >
        </div>
      </div>
    </el-drawer>
    <el-drawer title="" :visible.sync="followedDrawer" direction="rtl">
      <div class="user_follow">
        <div
          class="user_follow_item"
          v-for="data in followedData"
          :key="data.id"
        >
          <img :src="data.avatar == null ? userImg : data.avatar" />
          <span
            ><router-link target="_blank" :to="'/user/' + data.id">{{
              data.username
            }}</router-link></span
          >
        </div>
      </div>
    </el-drawer>
    <UserInfoEdit
      v-if="userEditDialog"
      ref="userEdit"
      @refreshUserInfo="getUserSimpleInfoById"
    ></UserInfoEdit>
  </div>
</template>

<script>
import UserInfoEdit from "./UserInfoEdit.vue";
import common from "@/api/common";
import cookie from "js-cookie";

export default {
  name: "UserInfo",
  components: { UserInfoEdit },
  data() {
    return {
      isFollow: false, //
      conditionList: [
        // 
        { index: "10000", category: "" },
        { index: "10001", category: "" },
        { index: "10002", category: "" },
      ],
      otherConditionList: [
        // 
        { index: "10000", category: "" },
        { index: "10001", category: "" },
      ],
      conditionIndex: "10000", // 
      datas: [],
      followData: [], //
      followedData: [], //
      noMore: false, //
      isAchiveBottom: false, //
      articleFlag: true, // false
      followDrawer: false, //
      followedDrawer: false, //
      userEditDialog: false, //
      userInfo: {
        //
        id: 100002, //id
        username: "kerco123", //
        sex: "", //
        followerCount: 1, //
        followedCount: 1, //
        avatar: null, //
        summary: null, //
        createTime: "2023-01-14T07:13:01", //
      },
      userOther: {
        //
        originalCount: 0, //
        thumbsUpCount: 0, //
        viewsCount: 0, //
      },
      userImg: require("@/assets/images/nologin.png"), //
      loginId: "", //id
      articleParams: {
        // 
        currentPage: 1, //
        isAchiveBottom: false, //
        noMore: false,
      },
      questionParams: {
        // 
        currentPage: 1,
        isAchiveBottom: false, //
        noMore: false,
      },
      messageParams: {
        // 
        currentPage: 1,
        isAchiveBottom: false, //
        noMore: false,
      },
      articleList: [], //
      questionList: [], //
      follow: {
        //
        followId: null, //id
        userId: null, //id
      },
    };
  },
  created() {
    //window.onscroll = function(){}this
    //thisvuethis
    window.onscroll = () => {
      //scrollTop
      var scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      //windowHeight
      var windowHeight =
        document.documentElement.clientHeight || document.body.clientHeight;
      //scrollHeight
      var scrollHeight =
        document.documentElement.scrollHeight || document.body.scrollHeight;
      //(20px)
      if (
        scrollTop + windowHeight >= scrollHeight - 20 &&
        !this.isAchiveBottom &&
        !this.noMore
      ) {
        // console.log(
        //   "" +
        //     scrollTop +
        //     "" +
        //     windowHeight +
        //     "" +
        //     scrollHeight
        // );
        if (this.conditionIndex == "10000") {
          this.articleParams.isAchiveBottom = true;
        } else if (this.conditionIndex == "10001") {
          this.questionParams.isAchiveBottom = true;
        }
        //
        setTimeout(() => {
          //
          //push
          if (this.conditionIndex == "10000") {
            this.articleParams.currentPage += 1;
            this.getUserPublishArticleList();
            this.articleParams.isAchiveBottom = false;
          } else if (this.conditionIndex == "10001") {
            this.questionParams.currentPage += 1;
            this.getUserPublishArticleList();
            this.questionParams.isAchiveBottom = false;
          }
        }, 500);
      }
    };
  },
  beforeMount() {
    this.getUserInfo();
  },
  mounted() {
    this.follow.followId = this.$route.params.id;
    this.getUserSimpleInfoById();
    this.getUserAchievementById(this.$route.params.id);
    this.handleSelect2("10000");
  },
  methods: {
    getUserInfo() {
      //
      let token = cookie.get("token");
      if (token === undefined) {
        return;
      }
      common.getUserInfo().then((resp) => {
        let data = resp.data.data;
        this.loginId = data.id;
        this.follow.userId = data.id;
        this.checkUserFollow();
      });
    },
    getUserSimpleInfoById() {
      //id
      let id = this.$route.params.id;
      //

      //
      common.getUserSimpleInfoById(id).then((resp) => {
        let data = resp.data.data;
        this.userInfo.id = data.id;
        this.userInfo.username = data.username;
        this.userInfo.sex = data.sex;
        this.userInfo.followerCount = data.followerCount;
        this.userInfo.followedCount = data.followedCount;
        this.userInfo.avatar = data.avatar;
        this.userInfo.summary = data.summary;
        this.userInfo.createTime = data.createTime;
      });
    },
    getUserAchievementById(id) {
      //id 
      common.getUserAchievementById(id).then((resp) => {
        let data = resp.data.data;
        this.userOther.originalCount = data.originalCount;
        this.userOther.thumbsUpCount = data.thumbsUpCount;
        this.userOther.viewsCount = data.viewsCount;
      });
    },
    checkUserFollow() {
      //
      //
      let token = cookie.get("token");
      if (token === undefined) {
        return;
      }

      common.checkUserFollow(this.follow).then((resp) => {
        //false true
        this.isFollow = resp.data.data;
      });
    },
    authorFollow() {
      //
      let token = cookie.get("token");
      if (token === undefined) {
        this.$message({
          message: "",
          type: "warning",
        });
        this.$router.push({ path: "/login" });
        return;
      }
      //TODO 
      common.userFollow(this.follow).then((resp) => {
        if (resp.data.data) {
          this.$message({
            message: "",
            type: "success",
          });
          this.isFollow = true;
        }
      });
    },
    undoAuthorFollow() {
      //TODO 
      common.userCancelFollow(this.follow).then((resp) => {
        if (resp.data.data) {
          this.$message({
            message: "",
            type: "success",
          });
          this.isFollow = false;
        }
      });
    },
    getUserPublishArticleList() {
      //

      //
      if (this.articleParams.currentPage == 1) {
        //id
        common
          .getUserPublishArticleList(
            this.$route.params.id,
            this.articleParams.currentPage
          )
          .then((resp) => {
            this.articleList = resp.data.data;
            if (this.articleList === undefined || resp.data.data.length < 10) {
              this.noMore = true;
              this.articleParams.noMore = true;
            }
          });
      } else {
        //id
        common
          .getUserPublishArticleList(
            this.$route.params.id,
            this.articleParams.currentPage
          )
          .then((resp) => {
            if (resp.data.data === undefined) {
              this.noMore = true;
              this.articleParams.noMore = true;
              return;
            }
            this.articleList = this.articleList.concat(resp.data.data);
            //10
            if (resp.data.data.length < 10) {
              this.noMore = true;
              this.articleParams.noMore = true;
            }
          });
      }
    },
    getUserPublishQuestionList() {
      //
      if (this.questionParams.currentPage == 1) {
        //id
        common
          .getUserPublishQuestionList(
            this.$route.params.id,
            this.questionParams.currentPage
          )
          .then((resp) => {
            this.questionList = resp.data.data;
            //10
            if (this.questionList === undefined || resp.data.data.length < 10) {
              this.noMore = true;
              this.questionParams.noMore = true;
            }
          });
      } else {
        //id
        common
          .getUserPublishQuestionList(
            this.$route.params.id,
            this.questionParams.currentPage
          )
          .then((resp) => {
            if (resp.data.data === undefined) {
              this.noMore = true;
              this.articleParams.noMore = true;
              return;
            }
            this.questionList = this.questionList.concat(resp.data.data);
            //10
            if (resp.data.data.length < 10) {
              this.noMore = true;
              this.questionParams.noMore = true;
            }
          });
      }
    },
    handleSelect2(index) {
      //
      this.conditionIndex = index;
      // 10000 10001
      if (index == "10000") {
        this.noMore = this.articleParams.noMore;
        this.isAchiveBottom = this.articleParams.isAchiveBottom;
        this.getUserPublishArticleList();
      } else if (index == "10001") {
        this.noMore = this.questionParams.noMore;
        this.isAchiveBottom = this.questionParams.isAchiveBottom;
        this.getUserPublishQuestionList();
      }
    },
    sendUserVisible() {
      //
      this.userEditDialog = true;
      this.$nextTick(() => {
        //
        this.$refs.userEdit.init(this.$route.params.id);
      });
    },
    refreshUserInfo() {
      //
      this.userEditDialog = false;
      this.getUserSimpleInfoById();
    },
    followClick() {
      //
      this.followDrawer = true;
      //
      if (this.followData.length > 0) {
        return;
      }
      common.getUserFollowList(this.$route.params.id).then((resp) => {
        this.followData = resp.data.data;
      });
    },
    followedClick() {
      //
      this.followedDrawer = true;
      //
      if (this.followedData.length > 0) {
        return;
      }
      common.getUserFollowedList(this.$route.params.id).then((resp) => {
        this.followedData = resp.data.data;
      });
    },
  },
};
</script>

<style scoped>
a {
  text-decoration: none;
  color: black;
}

b {
  color: black;
}

/*  */
.main {
  margin: 0 auto;
  margin-top: 140px;
  width: 1200px;
}

/*  */
.user_info {
  width: 100%;
  height: 165px;
  background-color: white;
}

/*  */
.user_img {
  width: 100%;
  text-align: center;
}

.user_img img {
  background-color: white;
  width: 70%;
  height: 130px;
  margin-top: -40px;
  border: 5px solid rgb(218, 237, 217);
  border-radius: 5px;
  padding: 5px 5px;
  box-shadow: 0px 0px 5px rgb(205, 205, 205);
}

/*  */
.user_title {
  margin-top: 15px;
  font-size: 19px;
  font-weight: 600;
}

/*  */
.user_brief {
  margin-top: 15px;
}

.user_brief span {
  display: inline-block;
  /* height: 100%; */
  margin-top: 5px;
  margin-right: 25px;
  color: gray;
  font-size: 14px;
}

.user_brief_content {
  display: flex;
  width: 100%;
  word-break: break-word;
}

.user_brief_content span:first-child {
  display: inline-block;
  margin-right: 0px;
  width: 11%;
}

.user_brief_content span:last-child {
  margin-right: 0px;
  height: 100px;
  width: 85%;
}

.user_achievement {
  width: 100%;
  height: 180px;
  border-left: 2px solid rgb(247, 247, 252);
}

/*  */
.user_achievement_title {
  text-align: center;
  font-size: 19px;
  font-weight: 600;

  height: 50px;
  border-bottom: 2px solid rgb(247, 247, 252);
}

.user_achievement_info {
  margin-top: 10px;
}
/*  */
.user_achievement_info_count {
  text-align: center;
  display: flex;
  margin: 0 auto;
  margin-top: 20px;
  width: 70%;
  height: 80px;
  /* background-color: gray; */
}

.user_achievement_info_item {
  display: inline-block;
  width: 33%;
  height: 100%;
  /* background-color: green; */
}

.user_achievement_info_item_label,
.user_achievement_info_item_number {
  margin-top: 10px;
}

.number_style {
  color: rgb(62, 175, 124);
}
/*  */
.author_follow {
  text-align: center;
}

.author_follow span {
  display: inline-block;
  width: 90px;
  height: 30px;
  background-color: rgb(62, 175, 124);
  border-radius: 5px;
  line-height: 30px;
  color: white;
}

.author_follow span:hover {
  cursor: pointer;
  background-color: rgb(50, 149, 104);
}

/*  */
.article_condition {
  width: 98%;
  background-color: white;
  height: 40px;
  border-bottom: 1px solid rgb(247, 247, 252);
}

/*  */
.article_condition .el-menu-item {
  height: 40px;
  line-height: 40px;
}

/*  */
.article_main {
  display: flex;
