<template>
  <div>
    <Header></Header>
    <div class="main">
      <div class="article_nav">
        <el-menu
          :default-active="activeIndex"
          class="el-menu-demo"
          mode="horizontal"
          active-text-color="#3EAF7C"
          @select="handleSelect"
        >
          <el-menu-item
            v-for="data in categoryList"
            :key="data.id"
            :index="data.id"
            >{{ data.label }}</el-menu-item
          >
        </el-menu>
      </div>
      <div class="article_sub_nav" v-show="activeIndex != 1">
        <span
          class="sub_tag"
          v-for="(data, index) in subTagList"
          :key="index"
          @click="subTagClick(data.id)"
        >
          <a href="javascript:void(0);">
            <!--   -->
            <span class="active" v-if="$route.query.tagId == data.id">{{
              data.label
            }}</span>
            <span v-else>{{ data.label }}</span>
          </a></span
        >
      </div>
      <div class="article_body">
        <el-row>
          <el-col :span="18">
            <div class="article_condition">
              <el-menu
                :default-active="form.condition"
                class="el-menu-demo"
                mode="horizontal"
                active-text-color="#3EAF7C"
                @select="handleSelect2"
              >
                <el-menu-item
                  v-for="data in conditionList"
                  :key="data.index"
                  :index="data.index"
                  >{{ data.category }}</el-menu-item
                >
              </el-menu>
            </div>
            <div class="article_main" v-for="data in datas" :key="data.id">
              <div v-if="data.firstImg != null" class="article_main_body">
                <p class="article-title">
                  <router-link target="_blank" :to="'/article/' + data.id">{{
                    data.title
                  }}</router-link>
                </p>
                <p class="article-tag">
                  <el-tag size="small" style="color: rgb(62, 175, 124);" color="white" v-if="data.official == 1"></el-tag>
                  <el-tag size="small" style="color: rgb(62, 175, 124);" color="white" v-if="data.top == 1"></el-tag>
                  <el-tag size="small" style="color: rgb(62, 175, 124);" color="white" v-if="data.essence == 1"></el-tag>
                  <el-tag
                    v-for="tag in data.tagList"
                    :key="tag.id"
                    type="info"
                    size="mini"
                    ><router-link target="_blank" :to="'/tag/' + tag.id">{{
                      tag.label
                    }}</router-link></el-tag
                  >
                </p>
                <p class="article-describe">
                  <router-link target="_blank" :to="'/article/' + data.id">
                    {{ data.content }}</router-link
                  >
                </p>
                <p class="article-other">
                  <img
                    :src="
                      data.authorAvatar == null ? userImg : data.authorAvatar
                    "
                  />
                  <span
                    ><router-link target="_blank" :to="'/user/' + data.authorId"
                      >{{ data.authorUsername }}&nbsp;&nbsp;</router-link
                    ></span
                  >
                  <span>{{ data.publishTime.split("T")[0] }}</span>
                  <span
                    ><i class="el-icon-view"></i>&nbsp;&nbsp;{{
                      data.views
                    }}</span
                  >
                </p>
              </div>
              <div v-else class="article_main_body_noImg">
                <p class="article-title">
                  <router-link target="_blank" :to="'/article/' + data.id">{{
                    data.title
                  }}</router-link>
                </p>
                <p class="article-tag">
                  <el-tag size="small" style="color: rgb(62, 175, 124);" color="white" v-if="data.official == 1"></el-tag>
                  <el-tag size="small" style="color: rgb(62, 175, 124);" color="white" v-if="data.top == 1"></el-tag>
                  <el-tag size="small" style="color: rgb(62, 175, 124);" color="white" v-if="data.essence == 1"></el-tag>
                  <el-tag
                    v-for="tag in data.tagList"
                    :key="tag.id"
                    type="info"
                    size="mini"
                    ><router-link target="_blank" :to="'/tag/' + tag.id">{{
                      tag.label
                    }}</router-link></el-tag
                  >
                </p>
                <p class="article-describe">
                  <router-link target="_blank" :to="'/article/' + data.id">
                    {{ data.content }}</router-link
                  >
                </p>
                <p class="article-other">
                  <img
                    :src="
                      data.authorAvatar == null ? userImg : data.authorAvatar
                    "
                  />
                  <span
                    ><router-link target="_blank" :to="'/user/' + data.authorId"
                      >{{ data.authorUsername }}&nbsp;&nbsp;</router-link
                    ></span
                  >
                  <span>{{ data.publishTime.split("T")[0] }}</span>
                  <span
                    ><i class="el-icon-view"></i>&nbsp;&nbsp;{{
                      data.views
                    }}</span
                  >
                </p>
              </div>
              <div v-if="data.firstImg != null" class="article_main_img">
                <router-link target="_blank" :to="'/article/' + data.id"
                  ><img :src="data.firstImg"
                /></router-link>
              </div>
            </div>
            <div class="loading">{{ noMore ? "" : "..." }}</div>
          </el-col>
          <el-col :span="6">
            <div :style="{ height: asideHeight + 'px' }" class="article_aside">
              <div class="article_bulletin_area">
                <div class="article_bulletin_tip">
                  <span></span>
                </div>
                <div class="article_bulletin_list">
                  <div
                    class="article_bulletin_node"
                    v-for="value in 4"
                    :key="value"
                  >
                    <el-row>
                      <el-col :span="16"
                        ><span class="title"
                          >123123123123123123123123123123</span
                        ></el-col
                      >
                      <el-col :span="8"
                        ><span style="color: rgb(132, 132, 141)"
                          >2023-01-02</span
                        ></el-col
                      >
                    </el-row>
                  </div>
                </div>
              </div>
              <div class="article_random_area">
                <div class="article_random_tip">
                  <span></span>
                </div>
                <div
                  class="article_random_node"
                  v-for="value in 5"
                  :key="value"
                >
                  <p class="article_random_title">
                    <router-link target="_blank" :to="'/article/' + value"
                      >aaaaaaaaaaaaa111111111111111111111</router-link
                    >
                  </p>
                  <p class="article_random_other">
                    <span
                      ><img
                        src="@/assets/images/nologin.png"
                      />&nbsp;<router-link target="_blank" to="/user/123123"
                        >123123</router-link
                      ></span
                    >
                    <span> <i class="fa-regular fa-thumbs-up"></i> 1000</span>
                  </p>
                </div>
              </div>
            </div>
          </el-col>
        </el-row>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>

<script>
import common from "@/api/common";

export default {
  name: "ArticleList",
  data() {
    return {
      activeIndex: "1",   //
      categoryList: [],   //
      conditionList: [    //
        { index: "10000", category: "" },
        { index: "10001", category: "" },
      ],
      subTagList: ["aaa"],  //
      datas: [],    //
      isAchiveBottom: false, //
      noMore: false,    //
      userImg: require("@/assets/images/nologin.png"),  //
      asideHeight: 170,   //sticky
      relationData: {},   // key:id value: 
      form: {     //
        categoryId: 1,    //id
        tagId: 1,   //id
        currentPage: 1,   //
        condition: "10000", //id
      },
    };
  },
  created() {
    //window.onscroll = function(){}this
    //thisvuethis
    window.onscroll = () => {
      //scrollTop
      var scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      //windowHeight
      var windowHeight =
        document.documentElement.clientHeight || document.body.clientHeight;
      //scrollHeight
      var scrollHeight =
        document.documentElement.scrollHeight || document.body.scrollHeight;

      // if (this.datas.length >= 10) {
      //   this.noMore = true;
      // }
      //(20px)
      console.log(
        scrollTop + windowHeight >= scrollHeight - 20 &&
          !this.isAchiveBottom &&
          !this.noMore
      );
      if (
        scrollTop + windowHeight >= scrollHeight - 20 &&
        !this.isAchiveBottom &&
        !this.noMore
      ) {
        // console.log(
        //   "" +
        //     scrollTop +
        //     "" +
        //     windowHeight +
        //     "" +
        //     scrollHeight
        // );
        this.isAchiveBottom = true;
        //
        setTimeout(() => {
          //
          //push
          this.form.currentPage = this.form.currentPage + 1;
          this.getArticleList();
          this.isAchiveBottom = false;
        }, 500);
      }
    };
  },
  beforeMount() {
    // 
  },
  mounted() {
    this.getCategoryTree();
    this.getArticleList();
  },
  methods: {
    getArticleList() {
      //
      //datas
      if (this.form.currentPage == 1) {
        common.getArticleListByCondition(this.form).then((resp) => {
          this.datas = resp.data.data;
          this.asideHeight = this.asideHeight + this.datas.length * 170;
        });
      } else {
        //datas
        let length = this.datas.length;
        common.getArticleListByCondition(this.form).then((resp) => {
          this.datas = this.datas.concat(resp.data.data);
          //10noMore = true
          if (resp.data.data.length < 10) {
            this.noMore = true;
          }
          this.asideHeight =
            this.asideHeight + (this.datas.length - length) * 170;
        });
      }
    },
    handleSelect(index) {
      //TODO 
      //1   subTagList

      //
      this.activeIndex = index;
      this.form.categoryId = index; //id
      this.form.tagId = 1;  //id1
      this.form.currentPage = 1;  //1
      this.form.condition = "10000";  //10000 
      this.asideHeight = 170; //
      //
      this.subTagList = this.relationData[index];
      //
      this.datas = [];
      this.$router.push({
        path: "/article",
        query: {
          categoryId: index,
          tagId: 1,
        },
      });
      this.getArticleList();
    },
    subTagClick(tagId) {
      //
      //
      let categoryId = this.$route.query.categoryId;
      this.form.categoryId = categoryId;
      this.form.tagId = tagId;
      this.asideHeight = 170;
      this.form.currentPage = 1;

      this.$router.push({
        path: "/article",
        query: {
          categoryId,
          tagId,
        },
      });
      this.getArticleList();
    },
    handleSelect2(index) {
      //
      this.asideHeight = 170;
      this.form.condition = index;
      this.getArticleList();
    },
    getCategoryTree() {
      //
      common.getCategoryTree().then((resp) => {
        let relationData = resp.data.data;
        this.categoryList.push({ id: "1", label: "" });
        for (let i = 0; i < relationData.length; i++) {
          this.categoryList.push({
            id: relationData[i].id + "",
            label: relationData[i].label,
          });
          this.relationData[relationData[i].id] = relationData[i].children;
          this.relationData[relationData[i].id].unshift({
            id: "1",
            label: "",
          });
        }
      });
    },
  },
};
</script>

<style scoped>
a {
  text-decoration: none;
  color: black;
}

/*  */
.main {
  margin: 0 auto;
  margin-top: 70px;
  width: 1200px;
}

/*   */
.article_nav {
  width: 100%;
  background-color: white;
  margin-bottom: 10px;
}

.article_nav span {
  display: inline-block;
  height: 100%;
  width: 100px;
}

/*  */
.el-menu-item {
  height: 50px;
  line-height: 50px;
}

/*    */
.article_sub_nav {
  width: 100%;
  margin-bottom: 10px;
}

.sub_tag {
  margin-top: 10px;
  margin-right: 10px;
  padding-left: 9px;
  padding-right: 9px;
  padding-top: 5px;
  /* padding-bottom: 2px; */
  display: inline-block;
  height: 24px;
  /* background-color: rgb(62, 175, 124); */
  background-color: white;
  font-size: 13px;
  border-radius: 8px;
}

/*  () */
.article_body {
  width: 100%;
  /* position: sticky; */
}

/*  */
.article_bulletin_area {
  width: 100%;
  height: 300px;
  background-color: white;
}

/*  /  */
.article_bulletin_tip,
.article_random_tip {
  height: 55px;
  width: 100%;
  /* background-color: green; */
  line-height: 55px;
  border-bottom: 1px solid rgb(247, 247, 252);
}

/* /  */
.article_bulletin_tip span,
.article_random_tip span {
  margin-left: 15px;
  font-size: 18px;
  font-weight: bolder;
}

.article_bulletin_list {
  width: 100%;
  height: 100%;
}

.article_bulletin_node {
  height: 47px;
  width: 100%;
  line-height: 47px;
  font-size: 14px;
}

.article_bulletin_node .title {
  display: inline-block;
  width: 80%;
  margin-left: 15px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/*   */
.article_random_area {
  position: sticky;
  top: 70px;
  /* bottom: 0; */
  margin-top: 20px;
  width: 100%;
  height: 460px;
  background-color: white;
}

/*   */
.article_random_node {
  height: 80px;
  width: 100%;
  /* background-color: yellow; */
}

/*  */
.article_random_title {
  padding-top: 18px;
  margin-left: 13px;
  width: 90%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/*  */
.article_random_other {
  margin-top: 10px;
}

/*  */
.article_random_other span {
  margin-left: 13px;
  display: inline-block;
  /* width: 30%; */
  font-size: 13px;
}

.article_random_other span img {
  width: 28px;
  vertical-align: middle; /*  */
}

.article_random_other span:last-child {
  width: 30%;
  float: right;
  /* background-color: yellow; */
  margin-top: 5px;
}

.article_condition {
  width: 98%;
  background-color: white;
  height: 40px;
  border-bottom: 1px solid rgb(247, 247, 252);
}

/*  */
.article_condition .el-menu-item {
  height: 40px;
  line-height: 40px;
}

/*  */
.article_main {
  display: flex;
  width: 98%;
  height: 170px;
  background-color: white;
  border-bottom: 1px solid rgb(247, 247, 252);
}

.article_main:hover {
  background-color: rgb(238, 253, 238);
  cursor: pointer; /*   */
}

/*   */
.loading {
  width: 98%;
  height: 60px;
  background-color: white;
  text-align: center;
  line-height: 60px;
  color: rgb(132, 132, 141);
}

/*   */
.article_main_body {
  display: inline-block;
  width: 80%;
  height: 100%;
  /* background-color: rgb(93, 177, 93); */
}

.article_main_body_noImg {
  display: inline-block;
  width: 100%;
  height: 100%;
  /* background-color: rgb(93, 177, 93); */
}

.article_main_img {
  display: inline-block;
  text-align: center;
  width: 20%;
  height: 100%;
  /* background-color: rgb(185, 185, 117); */
}

.article_main_img img {
  width: 90%;
  height: 90%;
  margin-top: 16px;
}

/*  */
.article-title {
  width: 95%;
  margin-top: 13px;
  margin-left: 13px;
  font-size: 21px;
  text-overflow: ellipsis; /*  */
  overflow: hidden; /* */
  white-space: nowrap; /* */
  font-weight: bolder; /* */
}

.article-title a:hover,
.answer-title a:hover,
.article_random_title a:hover {
  color: rgb(62, 175, 124);
}

/*  */
.article-tag {
  margin-left: 28px;
  margin-top: 5px;
  margin-bottom: 15px;
}

.article-tag .el-tag {
  margin-right: 5px;
}

/*  */
.article-describe {
  width: 95%;
  height: 60px;
  margin-left: 13px;
  font-size: 14px;
  color: gray;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;

  margin-top: -8px;
}

.article-describe a {
  color: gray;
}

/*  */
.article-other {
  /* margin-top: 8px; */
  margin-left: 13px;
  color: black;
}

.article-other img {
  margin-right: 5px;
  width: 30px;
  height: 30px;
  vertical-align: middle; /*  */
  border-radius: 30px;
}

.article-other span {
  display: inline-block;
  /* width: 100px; */
  margin-right: 20px;
  font-size: 14px;
  /* border-right: 1px solid grey; */
}

.article-other span:last-child {
  border-right: 0px;
}
</style>