<template>
  <div>
    <Header></Header>
    <div class="main">
      <el-row>
        <el-col :span="18">
          <div class="article_body">
            <!--  -->
            <p>{{ article.title }}</p>
            <!--  -->
            <div class="info">
              <img
                :src="
                  article.authorAvatar == null ? userImg : article.authorAvatar
                "
              />
              <span
                ><router-link
                  target="_blank"
                  :to="'/user/' + article.authorId"
                  >{{ article.authorUsername }}</router-link
                >&nbsp;&nbsp;</span
              >
              <span
                ><i class="el-icon-time"></i>&nbsp;{{
                  article.publishTime.split("T")[0]
                }}&nbsp;&nbsp;</span
              >
              <span><i class="el-icon-view"></i>&nbsp;{{ article.views }}</span>
            </div>
            <!-- md -->
            <mavon-editor
              style="box-shadow: 0px"
              previewBackground="#fff"
              class="md"
              :value="article.content"
              :subfield="prop.subfield"
              :defaultOpen="prop.defaultOpen"
              :toolbarsFlag="prop.toolbarsFlag"
              :editable="prop.editable"
              :scrollStyle="prop.scrollStyle"
            ></mavon-editor>
            <div class="other">
              <!--  -->
              <span>{{ article.title }}</span>
              <div
                style="
                  width: 23%;
                  float: right;
                  text-align: right;
                  margin-right: 16px;
                "
              >
                <span slot="reference"
                  ><a href="javascript:void(0)"
                    ><i class="fa-regular fa-thumbs-up"></i>&nbsp;{{
                      article.thumbsup
                    }}</a
                  ></span
                >
                <span
                  ><a href="javascript:void(0)"><i class="el-icon-share"></i></a
                ></span>
              </div>
            </div>
          </div>
          <div class="article_comment_body">
            <!--  -->
            <div class="article_comment_submit">
              <p
                style="
                  text-align: left;
                  margin-left: 20px;
                  margin-bottom: 13px;
                  font-size: 21px;
                  color: rgb(132, 132, 141);
                  font-weight: bolder;
                "
              >
                
              </p>
              <el-input
                type="textarea"
                :autosize="{ minRows: 4, maxRows: 4 }"
                placeholder=""
                v-model.lazy="commentAreaData"
                maxlength="200"
                show-word-limit
                resize="none"
              >
              </el-input>
              <p
                style="
                  text-align: right;
                  margin-right: 20px;
                  margin-top: 13px;
                  padding-bottom: 15px;
                "
              >
                <el-button @click="commentClick" plain></el-button>
              </p>
            </div>
            <!--  -->
            <div class="article_comment_area">
              <p
                style="
                  margin-left: 20px;
                  margin-bottom: 13px;
                  font-size: 21px;
                  color: rgb(132, 132, 141);
                  font-weight: bolder;
                "
              >
                
              </p>
              <!--  -->
              <template v-if="commentList != null">
                <div
                  v-for="comment in commentList"
                  :key="comment.id"
                  class="article_comment_node"
                >
                  <div class="item1">
                    <img
                      :src="
                        comment.userInfo.avatar == null
                          ? userImg
                          : comment.userInfo.avatar
                      "
                    />
                    <span
                      ><router-link
                        target="_blank"
                        :to="'/user/' + comment.userInfo.id"
                        >{{ comment.userInfo.username }}</router-link
                      ></span
                    >
                    <span>{{ comment.createTime.replace("T", " ") }}</span>
                    <span @click="replyClick(comment.id, comment.userInfo.id)"
                      ><a href="javascript:void(0);"
                        ><i class="fa-regular fa-comments"></i>&nbsp;</a
                      ></span
                    >
                  </div>
                  <div class="item2">
                    <span>{{ comment.commentContent }}</span>
                  </div>
                  <div v-if="comment.subComment != null" class="item3">
                    <div
                      v-for="sub in comment.subComment"
                      :key="sub.key"
                      class="article_comment_sub_node"
                    >
                      <div class="sub_item1">
                        <img
                          :src="
                            sub.userInfo.avatar == null
                              ? userImg
                              : sub.userInfo.avatar
                          "
                        />
                        <span
                          ><router-link
                            target="_blank"
                            :to="'/user/' + sub.userInfo.id"
                            >{{ sub.userInfo.username }}</router-link
                          ></span
                        >
                        <span></span>
                        <span style="color: black">{{
                          sub.replyUsername
                        }}</span>
                        <span>{{ sub.createTime.replace("T", " ") }}</span>
                        <span @click="replyClick(sub.parentId, sub.userInfo.id)"
                          ><a href="javascript:void(0);"
                            ><i class="fa-regular fa-comments"></i>&nbsp;</a
                          ></span
                        >
                      </div>
                      <div class="sub_item2">
                        <span>{{ sub.commentContent }}</span>
                      </div>
                    </div>
                  </div>
                </div>
              </template>
              <el-empty v-else description=".."></el-empty>
            </div>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="article_aside">
            <div class="article_author_area">
              <div class="article_author_avator">
                <img
                  style="margin-top: 30px"
                  :src="
                    article.authorAvatar == null
                      ? userImg
                      : article.authorAvatar
                  "
                />
                <p style="margin-top: 10px; margin-bottom: 10px">
                  <router-link target="_blank" to="/user/123123">{{
                    this.article.authorUsername
                  }}</router-link>
                </p>

                <p
                  style="margin-top: 5px; margin-bottom: 10px"
                  class="article_author_follow"
                  v-if="follow.followId != follow.userId"
                >
                  <span @click="authorFollow" v-if="!isFollow"
                    ><i class="el-icon-plus"></i></span
                  >
                  <span @click="undoAuthorFollow" v-else></span>
                </p>
              </div>
              <div class="article_author_info">
                <div class="info_item">
                  <el-row>
                    <el-col :span="12"
                      ><span> {{ user.originalCount }}</span></el-col
                    >
                    <el-col :span="12"
                      ><span> {{ user.followedCount }}</span></el-col
                    >
                  </el-row>
                </div>
                <div class="info_item">
                  <el-row>
                    <el-col :span="12"
                      ><span> {{ user.thumbsUpCount }}</span></el-col
                    >
                    <el-col :span="12"
                      ><span> {{ user.collectionCount }}</span></el-col
                    >
                  </el-row>
                </div>
              </div>
            </div>
            <div class="article_random_area">
              <div class="article_random_tip">
                <span></span>
              </div>
              <div class="article_random_node" v-for="value in 5" :key="value">
                <p class="article_random_title">
                  <router-link target="_blank" :to="'/article/' + value"
                    >aaaaaaaaaaaaa111111111111111111111</router-link
                  >
                </p>
                <p class="article_random_other">
                  <span
                    ><img src="@/assets/images/nologin.png" />&nbsp;<router-link
                      target="_blank"
                      to="/user/123123"
                      >123123</router-link
                    ></span
                  >
                  <span> <i class="fa-regular fa-thumbs-up"></i> 1000</span>
                </p>
              </div>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>
    <!--  -->
    <template>
      <el-dialog
        title=""
        :visible.sync="replayDialogVisible"
        width="30%"
        :before-close="handleClose"
      >
        <el-input
          type="textarea"
          :autosize="{ minRows: 4, maxRows: 4 }"
          placeholder=""
          v-model.lazy="replyAreaData"
          maxlength="200"
          show-word-limit
          resize="none"
        >
        </el-input>
        <span slot="footer" class="dialog-footer">
          <el-button @click="replayDialogVisible = false"> </el-button>
          <el-button type="primary" @click="replyCommentClick"> </el-button>
        </span>
      </el-dialog>
    </template>
    <Footer></Footer>
  </div>
</template>

<script>
import common from "@/api/common";
import cookie from "js-cookie";

export default {
  name: "ArticleDetail",
  data() {
    return {
      prop: {
        subfield: false, // 
        defaultOpen: "preview", //edit   preview 
        editable: false,
        toolbarsFlag: false,
        scrollStyle: true,
      },
      commentAreaData: "",    //
      replyAreaData: "",      //
      replayDialogVisible: false,   //
      isFollow: false,      // false true
      userImg: require("@/assets/images/nologin.png"),    //
      article: {      //
        title: "",    //
        content:    //
          '#  JAVA\n## 1.java\nJava\n```java\npublic class HelloWorld{\n    public static void main(String[] args){\n\tSystem.out.println("Hello World,guys");\n    }\n}\n```',
        authorId: "", //id
        authorUsername: "", //
        authorAvatar: "",   //
        thumbsup: "",     //
        views: "",        //
        publishTime: "",  //
      },
      user: {       //
        originalCount: 0,   //
        thumbsUpCount: 0,   //
        followedCount: 0,   //
        collectionCount: 0, //
      },
      commentList: [],  //
      postComment: {    // 
        id: this.$route.params.id,  //id
        commentId: null,    //id
        replyId: null,      //id
        parentId: null,     // id
        commentContent: "",   //
      },
      follow: {       //
        followId: null, //id
        userId: null,   //id
      },
    };
  },
  mounted() {
    //
    this.getUserInfo();
    this.getArticle();
  },
  methods: {
    getUserInfo() {
      //
      let token = cookie.get("token");
      if (token === undefined) {
        return;
      }
      common.getUserInfo().then((resp) => {
        let data = resp.data.data;
        this.postComment.commentId = data.id;
        this.follow.userId = data.id;
      });
    },
    checkUserFollow() {
      //
      //
      let token = cookie.get("token");
      if (token === undefined) {
        return;
      }

      common.checkUserFollow(this.follow).then((resp) => {
        //false true
        this.isFollow = resp.data.data;
      });
    },
    getArticleComment(id) {
      //id 
      common.getArticleComment(id).then((resp) => {
        this.commentList = resp.data.data;
      });
    },
    getUserAchievementById(id) {
      //id 
      common.getUserAchievementById(id).then((resp) => {
        let data = resp.data.data;
        this.user.originalCount = data.originalCount;
        this.user.thumbsUpCount = data.thumbsUpCount;
        this.user.followedCount = data.followedCount;
        this.user.collectionCount = data.collectionCount;
        //
        this.checkUserFollow();
      });
    },
    getArticle() {
      //
      let id = this.$route.params.id;
      common.getArticleShowById(id).then((resp) => {
        let data = resp.data.data;
        this.article.content = data.content;
        this.article.title = data.title;
        this.article.authorId = data.authorId;
        this.article.authorUsername = data.authorUsername;
        this.article.authorAvatar = data.authorAvatar;
        this.article.thumbsup = data.thumbsup;
        this.article.views = data.views;
        this.article.publishTime = data.publishTime;

        //
        this.follow.followId = data.authorId;

        //
        this.getUserAchievementById(data.authorId);
        this.getArticleComment(data.id);
      });
    },
    commentClick() {
      //
      let token = cookie.get("token");
      if (token === undefined) {
        this.$message({
          message: "",
          type: "warning",
        });
        this.$router.push({ path: "/login" });
        return;
      }

      //TODO 
      if (this.commentAreaData.length == 0) {
        this.$message({
          message: "",
          type: "warning",
        });
        return;
      }
      //parentIdreplyIdnull
      this.postComment.parentId = null;
      this.postComment.replyId = null;
      //
      this.postComment.commentContent = this.commentAreaData;
      common.postArticleComment(this.postComment).then((resp) => {
        if (resp.data.code == 200) {
          this.$message({
            message: "",
            type: "warning",
          });
          //
          this.getArticleComment(this.postComment.id);
        }
      });
    },
    replyClick(parentId, replyId) {
      //
      let token = cookie.get("token");
      if (token === undefined) {
        this.$message({
          message: "",
          type: "warning",
        });
        this.$router.push({ path: "/login" });
        return;
      }

      //TODO 
      this.replayDialogVisible = true;
      //
      this.replyAreaData = "";
      this.postComment.parentId = parentId;
      this.postComment.replyId = replyId;
    },
    replyCommentClick() {
      // 
      //
      if (this.replyAreaData.length == 0) {
        this.$message({
          message: "",
          type: "warning",
        });
        return;
      }
      //
      this.postComment.commentContent = this.replyAreaData;
      common.postArticleComment(this.postComment).then((resp) => {
        if (resp.data.code == 200) {
          this.$message({
            message: "",
            type: "warning",
          });
          //
          this.getArticleComment(this.postComment.id);
        }
      });
      //
      this.replayDialogVisible = false;
    },
    authorFollow() {
      //
      let token = cookie.get("token");
      if (token === undefined) {
        this.$message({
          message: "",
          type: "warning",
        });
        this.$router.push({ path: "/login" });
        return;
      }
      //TODO 
      common.userFollow(this.follow).then((resp) => {
        if (resp.data.data) {
          this.$message({
            message: "",
            type: "success",
          });
          this.isFollow = true;
        }
      });
    },
    undoAuthorFollow() {
      //TODO 
      common.userCancelFollow(this.follow).then((resp) => {
        if (resp.data.data) {
          this.$message({
            message: "",
            type: "success",
          });
          this.isFollow = false;
        }
      });
    },
  },
};
</script>

<style scoped>
a {
  text-decoration: none;
  color: black;
}
/*  */
.main {
  margin: 0 auto;
  margin-top: 70px;
  width: 1200px;
}

.article_aside {
  position: sticky;
}

/*  */
.article_author_area {
  width: 100%;
  height: 250px;
  background-color: white;
}
/*  */
.article_author_avator {
  width: 100%;
  /* height: 100px; */
  text-align: center;
}

.article_author_avator img {
  width: 70px;
  height: 70px;
  border-radius: 45px;
}

.article_author_avator .article_author_follow span {
  display: inline-block;
  width: 90px;
  height: 30px;
  background-color: rgb(62, 175, 124);
  line-height: 30px;
  color: white;
}

.article_author_avator .article_author_follow span:hover {
  background-color: rgb(50, 149, 104);
  cursor: pointer;
}

/*  */
.article_author_info {
  height: 100%;
  width: 100%;
  /* background-color: grey; */
}

.info_item {
  height: 25px;
  margin-left: 30px;
  font-size: 14px;
  color: rgb(132, 132, 141);
}

/*  /  */
.article_random_tip {
  height: 55px;
  width: 100%;
  /* background-color: green; */
  line-height: 55px;
  border-bottom: 1px solid rgb(247, 247, 252);
}

/* /  */
.article_random_tip span {
  margin-left: 15px;
  font-size: 18px;
  font-weight: bolder;
}
/*   */
.article_random_area {
  position: sticky;
  top: 0px;
  margin-top: 20px;
  width: 100%;
  height: 460px;
  background-color: white;
}

/*   */
.article_random_node {
  height: 80px;
  width: 100%;
  /* background-color: yellow; */
}

/*  */
.article_random_title {
  padding-top: 18px;
  margin-left: 13px;
  width: 90%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.article_random_title a:hover {
  color: rgb(62, 175, 124);
}

/*  */
.article_random_other {
  margin-top: 10px;
}

/*  */
.article_random_other span {
  margin-left: 13px;
  display: inline-block;
  font-size: 13px;
}

.article_random_other span img {
  width: 28px;
  vertical-align: middle; /*  */
}

.article_random_other span:last-child {
  width: 30%;
  float: right;
  /* background-color: yellow; */
  margin-top: 5px;
}

/*  */
.article_body {
  width: 98%;
  background-color: white;
}

.article_body p {
  padding-top: 25px;
  margin-left: 25px;
  font-size: 32px;
  font-weight: bolder;
}

/*  */
.other {
  border-top: 2px solid rgb(247, 247, 252);
  width: 98%;
  padding-top: 15px;
  padding-left: 25px;
  padding-bottom: 15px;
  color: rgb(99, 99, 104);
}

.other div span {
  margin-right: 15px;
}

/*  */
.info {
  margin-top: 25px;
  font-size: 14px;
  color: rgb(132, 132, 141);
}

.info img {
  margin-left: 25px;
  width: 35px;
  height: 35px;
  vertical-align: middle; /*  */
  border-radius: 25px;
}

.info span {
  margin-left: 15px;
  border-right: 1px solid rgb(178, 173, 173);
}

.info span a {
  text-decoration: none;
  color: black;
}

.info span:last-child {
  border-right: 0px;
}

/*  */
.article_comment_body {
  margin-top: 15px;
  width: 98%;
  background-color: white;
}

/*  */
.article_comment_submit {
  text-align: center;
  width: 100%;
  padding-top: 13px;
  border-bottom: 1px solid rgb(247, 247, 252);
}

/*  */
.article_comment_submit .el-textarea {
  width: 97%;
}

/*  */
.article_comment_area {
  width: 100%;
  padding-top: 15px;
}

.article_comment_node {
  margin-left: 16px;
  padding-bottom: 10px;
}

/*   */
/*  */
.item1 img {
  width: 40px;
  height: 40px;
  vertical-align: middle;
  border-radius: 25px;
}
/*  */
.item1 span {
  margin-left: 15px;
  display: inline-block;
  height: 40px;
  color: rgb(132, 132, 141);
}

.item1 span:last-child,
.sub_item1 span:last-child {
  float: right;
  text-align: right;
  margin-right: 30px;
}

.item1 span:last-child {
  margin-right: 45px;
}

.item1 span a,
.item3 .sub_item1 span a {
  text-decoration: none;
  color: black;
}

/*   */
/*  */
.item2 {
  width: 92%;
  margin-left: 55px;
  padding-bottom: 10px;
}

.item2 span {
  font-size: 14px;
}

/*  */
.article_comment_sub_node {
  margin-bottom: 10px;
}

.item3 {
  width: 92%;
  margin-left: 55px;
}

.item3 .sub_item1 img {
  width: 30px;
  height: 30px;
  vertical-align: middle; /*  */
  border-radius: 25px;
}

.item3 .sub_item1 span {
  margin-left: 15px;
  display: inline-block;
  height: 30px;
  color: rgb(132, 132, 141);
}

.item3 .sub_item2 {
  margin-left: 45px;
  width: 94%;
  font-size: 14px;
}

/*  */
.reply_box {
  width: 91%;
  margin-left: 55px;
  margin-bottom: 15px;
}

/* mavon-editor css */
div /deep/ .hljs {
  color: black;
  background: #f6f8fa;
}
</style>